const memo = (fn) => {
  const fnValues = {};
  return (...args) => {
    const argsKey = args.toString();
    if (argsKey in fnValues) {
      return fnValues[argsKey];
    } else {
      return (fnValues[argsKey] = fn(...args));
    }
  };
};

const square = (n) => {
  console.log("invoked");
  return n * n;
};

const memoSquare = memo(square);

const multiply = (n) => {
  console.log("invoked");
  return n * 2;
};

const memoMultiply = memo(multiply);

const sum = (a, b) => {
  console.log("invoked");
  return a + b;
};

const memoSum = memo(sum);

console.log(memoMultiply(5));
console.log(memoSquare(5));
console.log(memoSum(4, 5));
